@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');


body , p , span ,a ,h1 , h2 , h3 , h4 ,h5 , h6{
    font-family: 'Poppins', sans-serif !important;
}



#demoPage #loading {
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 2000;
    top: 0;
    background: #fff
}
body, html {
    overflow-x: inherit !important;
}
#demoPage .loader {
    width: 100px;
    height: 80px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto
}

#demoPage .loader .image {
    width: 100px;
    height: 160px;
    font-size: 40px;
    text-align: center;
    -webkit-transform-origin: bottom center;
    transform-origin: bottom center;
    -webkit-animation: rotate 3s infinite;
    animation: rotate 3s infinite;
    opacity: 0
}

#demoPage .loader span {
    display: block;
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 0
}

#demoPage body {
    font-weight: 400;
    color: #000;
    font-size: 14px;
    -webkit-font-kerning: auto;
    -webkit-font-smoothing: antialiased;
    -webkit-backface-visibility: visible!important;
    text-align: center;
    background: #fcfcfc url(webpack:///mini-css-extract-plugin/_next/static/media/bg.c13132bb.png) no-repeat fixed 0 0/cover
}

#demoPage .wrapper {
    background: none;
    margin: 0 auto;
    width: 100%;
    float: left;
    position: relative
}

#demoPage .wrapper:before {
    content: "";
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: hsla(0,0%,100%,.2)
}

#demoPage .bg-top {
    width: 100%;
    height: 720px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1
}

#demoPage a {
    outline: none
}

#demoPage img {
    height: auto;
    max-width: 100%
}

#demoPage a:focus,#demoPage a:hover {
    outline: none;
    text-decoration: none
}

#demoPage header,#demoPage section {
    position: relative
}

#demoPage .logo {
    padding: 25px 60px;
    display: inline-block;
    background: rgba(19,5,80,.8);
    margin-bottom: 40px
}

#demoPage .logo-title {
    color: #feac01;
    display: block;
    font-size: 11px;
    font-weight: 600;
    letter-spacing: 2.5px;
    padding-top: 3px;
    text-transform: uppercase
}

#demoPage .content {
    padding: 10px 0 5px
}

#demoPage .text_heading {
    font-weight: 500;
    font-size: 60px;
    color: #feac01;
    line-height: 35px
}

#demoPage .text_heading span {
    font-size: 14px
}

#demoPage .shop-name {
    font-weight: 900;
    font-size: 18px;
    color: #fff;
    text-transform: uppercase;
    margin: 5px 0;
    display: block;
    text-align: center;
    padding: 10px 0;
    background: #feac01;
    opacity: .7
}

#demoPage .image-bg {
    background-image: url(webpack:///mini-css-extract-plugin/_next/static/media/bg-macbook.c31ac975.png);
    width: 560px;
    height: 340px;
    display: inline-block;
    position: relative
}

#demoPage .image-bg-big {
    background-image: url(webpack:///mini-css-extract-plugin/_next/static/media/bg-macbook-big.2210353e.png);
    width: 1020px;
    height: 593px;
    display: inline-block;
    position: relative
}

#demoPage .icon-new {
    background-color: #d6644a;
    border-radius: 50%;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    color: #fff;
    font-size: 200%;
    font-weight: 700;
    height: 100px;
    line-height: 100px;
    position: absolute;
    right: 20px;
    text-align: center;
    text-transform: uppercase;
    width: 100px;
    top: -35px;
    z-index: 9
}

#demoPage .image-bg .image-shop-scroll {
    background-position: center 0;
    background-repeat: no-repeat;
    position: absolute;
    width: 405px;
    height: 255px;
    top: 32px;
    left: 79px
}

#demoPage .image-bg .glare {
    position: absolute;
    width: 250px;
    height: 300px;
    top: 10px;
    right: 59px
}

#demoPage .image-bg .image-shop-scroll {
    transition: all 9s ease-out 0s;
    -webkit-transition: all 9s ease-out 0s;
    -moz-transition: all 9s ease-out 0s;
    -ms-transition: all 9s ease-out 0s;
    -o-transition: all 9s ease-out 0s
}

#demoPage .image-bg:hover .image-shop-scroll {
    background-position: center 100%!important
}

#demoPage .image-bg-big .image-shop-scroll {
    background-position: center 0;
    background-repeat: no-repeat;
    position: absolute;
    width: 780px;
    height: 500px;
    top: 32px;
    left: 129px
}

#demoPage .image-bg-big .glare {
    position: absolute;
    width: 250px;
    height: 300px;
    top: 10px;
    right: 59px
}

#demoPage .image-bg-big .image-shop-scroll {
    transition: all 5s ease-out 0s;
    -webkit-transition: all 5s ease-out 0s;
    -moz-transition: all 5s ease-out 0s;
    -ms-transition: all 5s ease-out 0s;
    -o-transition: all 5s ease-out 0s
}

#demoPage .image-bg-big:hover .image-shop-scroll {
    background-position: center 100%!important
}

#demoPage .link:hover .shop-name {
    color: #f4f4f4!important
}

@media (max-width: 480px) {
    #demoPage .text_heading {
        font-size:40px
    }

    #demoPage .image-bg {
        background-size: 320px 220px;
        height: 220px;
        margin-left: -20px;
        width: 320px
    }

    #demoPage .image-bg .image-shop-scroll {
        background-position: center 0;
        background-repeat: no-repeat;
        height: 159px;
        left: 54px;
        position: absolute;
        top: 26px;
        width: 215px
    }

    #demoPage .glare {
        display: none
    }
}

#demoPage .corner-ribbon {
    width: 200px;
    background: #e43;
    position: absolute;
    top: 25px;
    left: -50px;
    text-align: center;
    line-height: 30px;
    letter-spacing: 1px;
    color: #f0f0f0;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg)
}

#demoPage .corner-ribbon a {
    color: #fff
}

#demoPage .corner-ribbon.sticky {
    position: fixed
}

#demoPage .corner-ribbon.shadow {
    -webkit-box-shadow: 0 0 3px rgba(0,0,0,.3);
    box-shadow: 0 0 3px rgba(0,0,0,.3)
}

#demoPage .corner-ribbon.top-left {
    top: 25px;
    left: -50px;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    letter-spacing: 8px;
    font-size: 15px
}

#demoPage .corner-ribbon.bottom-left {
    top: auto;
    bottom: 25px;
    left: -50px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    font-weight: 700;
    width: 250px
}

#demoPage .corner-ribbon.red {
    background: #000
}

#demoPage .corner-ribbon.orange {
    background: #e82
}

