.icofont-ul {
    list-style-type: none;
    padding-left: 0;
    margin-left: 0
}

.icofont-ul>li {
    position: relative;
    line-height: 2em
}

.icofont-ul>li .icofont {
    display: inline-block;
    vertical-align: middle
}

.icofont-border {
    border: .08em solid #f1f1f1;
    border-radius: .1em;
    padding: .2em .25em .15em
}

.icofont-pull-left {
    float: left
}

.icofont-pull-right {
    float: right
}

.icofont.icofont-pull-left {
    margin-right: .3em
}

.icofont.icofont-pull-right {
    margin-left: .3em
}

.icofont-spin {
    -webkit-animation: icofont-spin 2s linear infinite;
    animation: icofont-spin 2s linear infinite;
    display: inline-block
}

.icofont-pulse {
    -webkit-animation: icofont-spin 1s steps(8) infinite;
    animation: icofont-spin 1s steps(8) infinite;
    display: inline-block
}

@-webkit-keyframes icofont-spin {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(1turn);
        transform: rotate(1turn)
    }
}

@keyframes icofont-spin {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0)
    }

    to {
        -webkit-transform: rotate(1turn);
        transform: rotate(1turn)
    }
}

.icofont-rotate-90 {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg)
}

.icofont-rotate-180 {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg)
}

.icofont-rotate-270 {
    -webkit-transform: rotate(270deg);
